@import url(https://fonts.googleapis.com/earlyaccess/cwtexfangsong.css);
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "cwTeXFangSong", serif, helvetica, sans-serif;
}

.App-header * {
  font-family: "Pangolin", "ChalkboardSE-Light", "Comic Sans MS", "Comic Sans", cursive, sans-serif;
}

.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-image: url(/tblin_website_test/static/media/background.61f086ef.png);
  background-size: 100vw 100vh;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
}
p, ol {
  margin: 12px 0;
  padding: 0;
  font-size: 19px;
}

h3 {
  font-size: 28px;
}


.App-header {
  padding: 0;
  width: 100%;
  min-height: 10vh;
  display: inline-block;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: rgb(120,0,0);
  position: fixed;
  top: 0;
  z-index: 50;
}

.title {
  margin: 10px 0 0 0;
}
.App-link {
  color: #61dafb;
}

.content {
  position: relative;
  top: 250px;
  z-index: 3;
  height: 95vh;
  overflow-y: auto;
  overflow-x: hidden; 
  font-size: 18px;
}

.column {
  vertical-align: text-top;
}
* .extended {
  padding-bottom: 300px;
}
.big {
  width: 48%;
  margin-right: 10%;
  text-align: left;
}

.small {
  width: 30%;
}
.left {
  clear: both;
  float: left;
  margin-left: 10%;
}
.right {
  margin-left: 0;
  float: right;
}

.photo {
  max-width: 400px;
  max-height: 400px;
  margin-left: -100px;
  margin-top: 20px;
}

.footer {
  height: 40px;
  width: 100%;
  position: fixed;
  bottom: 0;
  text-align: center;
  color: white;
  background-color: rgb(120,0,0);
}
.static_profile {
  position: fixed;
  top: 240px;
}
.static_publications {
  position: fixed;
  top: 240px;
  left: 70%;
}
.static_team {
  position: fixed;
  top: 300px;
  left: 5%;
}
.static_others {
  position: fixed;
  top: 300px;
  left: 70%;
}
.navbar-item {
  width: 10%;
}
.navbar-item:hover {
  cursor: pointer;
}
.navbar-click span{
  padding: 0 10px 3px 10px;
  border-radius: 100px;
  background-color: rgb(120,0,0);
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar-item {
	display: inline-block; 
	margin: 15px 45px 5px 45px;
	font-size: 3vh;
	
}
.navbar-item img {
	height: 100px;
	width: auto;
}
.profileIntro {
	margin-left: 15px;
	margin-bottom: 30px;
}

.profileOthers p, .profileOthers table, .profileOthers ol{
	margin-left: 30px;
}
.profileOthers h3 {
	margin-top: 20px;
}
.info img {
	width: 50%;
}
.info {
	margin-top: 30px;
	text-align: center;
}
.info p {
	font-size: 16px;
	width: 90%;
	margin-left: 10%;
	text-align: left;
	padding: 10px 10% 0 12%;
}
.publications_content li {
	margin-left: 30px;
}
.projects_content li {
	margin-left: 30px;
}
